/* Estilos básicos */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f9;
    color: #333;
}

/* Cabecera */
header {
    background: #333;
    color: white;
    padding: 20px 0;
    text-align: center;
}

header h1 {
    margin: 0;
    font-size: 2em;
}

header p {
    font-size: 1.2em;
}

nav ul {
    list-style: none;
    padding: 0;
}

nav ul li {
    display: inline-block;
    margin: 0 10px;
}

nav ul li a {
    color: white;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 15px;
    border-radius: 5px;
    transition: background-color 0.3s;
}

nav ul li a:hover {
    background-color: #575757;
}

/* Secciones */
.seccion {
    display: none;
    padding: 20px;
    margin: 20px auto;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 800px;
}

.seccion.activa {
    display: block;
}

.seccion h2 {
    font-size: 1.8em;
    margin-bottom: 20px;
}

.seccion p {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 15px;
}

/* Estilos para las imágenes del QR */
#qrcode {
    margin-top: 20px;
    display: inline-block;
    max-width: 200px;
    height: auto;
    margin-right: 15px;
}

/* Centrar el h2 de la sección */
#contact h2 {
    font-size: 1.8em;
    margin-bottom: 20px;
    text-align: center;
}

/* Estilos para el párrafo de la sección */
#contact p {
    font-size: 1.1em;
    line-height: 1.6;
    text-align: center;
}

/* Estilos para el contenedor de las imágenes QR */
#contact .qr-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
}

#contact img {
    max-width: 200px;
    height: auto;
    margin-right: 0;
}

/* Contenedor principal para el contenido */
.container {
    flex: 1; /* Hace que el contenido ocupe el espacio disponible */
    padding: 20px;
    background-color: #f4f4f9;
}

/* Asegura que el footer no cubra el contenido */
footer {
    background-color: #333;
    color: white;
    text-align: center;
    padding: 10px;
    position: fixed;
    bottom: -60px; /* Inicialmente fuera de la vista */
    left: 0;
    width: 100%;
    z-index: 1000;
    transition: bottom 0.3s ease-in-out; /* Transición suave */
}

/* Footer visible */
footer.visible {
    bottom: 0; /* Ponerlo visible cuando se llegue al final de la página */
}


/* Asegura que el contenido no se vea cubierto por el footer */
body {
    margin: 0;
    padding-bottom: 50px;
}

.container {
    padding-bottom: 60px;
}

/* Media queries para hacer el diseño responsive */
@media screen and (max-width: 768px) {
    header h1 {
        font-size: 1.5em;
    }

    header p {
        font-size: 1em;
    }

    nav ul {
        text-align: center;
    }

    nav ul li {
        display: block;
        margin: 5px 0;
    }

    .seccion {
        margin: 10px;
    }

    #contact img {
        max-width: 150px;
        margin-right: 10px;
    }

    #contact .qr-container {
        flex-direction: column;
        gap: 10px;
    }

    footer {
        padding: 15px;
    }

    .seccion h2 {
        font-size: 1.5em;
    }

    .seccion p {
        font-size: 1em;
    }
}

@media screen and (max-width: 480px) {
    header h1 {
        font-size: 1.3em;
    }

    header p {
        font-size: 0.9em;
    }

    .seccion h2 {
        font-size: 1.4em;
    }

    .seccion p {
        font-size: 0.9em;
    }

    #contact img {
        max-width: 120px;
        margin-right: 5px;
    }

    #contact .qr-container {
        flex-direction: column;
        gap: 5px;
    }

    footer {
        padding: 12px;
    }

    header h1 {
        margin-bottom: 20px;
    }

    header p {
        margin-bottom: 15px;
    }

    nav ul li {
        margin: 10px 0;
    }

    nav ul li a {
        padding: 12px 20px;
    }
}
